#Main {
  text-align: center;
  background-color: #000000;
  overflow: hidden;
  height: 100vh;
}

#stars {
  animation: starsMove infinite 25s linear;
  background-image: url(./resources/stars.png);
  background-size: 200vw;
  height: 100vh;
}

#spaceship {
  animation: moveUpDown infinite 5s ease-in-out;
  height: 40vh;
}

#Welcome-header {
  font-family: 'Press Start 2P', cursive;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#Attributes-header {
  font-family: 'Press Start 2P', cursive;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#startButton {
  font-family: 'Press Start 2P', cursive;
  font-size: 8vh;
  position: relative;
  top: 50vh;
  left: 20vw;
}

#name {
  font-family: 'Press Start 2P', cursive;
  font-size: 5vh;
  width: 60vw;
  left: 5vw;
  position: relative;
  bottom: 3vh;
}

#easyButton, #mediumButton, #hardButton {
  font-family: 'Press Start 2P', cursive;
  font-size: 8vh;
  position: relative;
  right: 25vw;
  margin: 1vh;
}

#totalPoints {
  font-family: 'Press Start 2P', cursive;
  font-size: 8vh;
  position: relative;
  right: 25vw;
  margin: 3vh;
  top: 5vh;
  color: white;
}

#submitButton {
  font-family: 'Press Start 2P', cursive;
  font-size: 8vh;
  position: relative;
  top: 10vh;
  left: 15vh;
}

#Attribute-Table {
  font-family: 'Press Start 2P', cursive;
  color: white;
  width: 50vw;
  height: 40vh;
  align-self: center;
  position: relative;
  bottom: 40vh;
  right: 5vh;
  border-collapse: collapse;
  border: 1px solid white;
}

tr, th, td {
  font-family: 'Press Start 2P', cursive;
  color: white;
  width: 6vw;
  height: 12vh;
  align-self: center;
  border-collapse: collapse;
  border: 1px solid white;
  margin: 10px;
}

#pskill, #fskill, #mskill, #eskill {
  width: 3vw;
}

#p1, #f1, #m1, #e1 {
  background-color: #00ff00;
}

@keyframes moveUpDown {
  0% {
    transform: translateY(0px);
  } 50% {
    transform: translateY(-50px);
  } 100% {
    transform: translateY(0px)
  }
}

@keyframes starsMove {
  0% {
    background-position: 100vw 0;
  } 100% {
    background-position: -100vw 0;
  }
}

@keyframes nextScreen {
  0% {
    transform: translateX(0vw);
  } 100% {
    transform: translateX(100vw);
  }
}